$primaryColor:#f00;
$textColor:#fff;

.wrapper{
    width:30vw;
    height:calc(100vh - 50px);
    margin:0 auto;
    background-color:rgba(0,0,0,.2);
    position: relative;
}
div{
    color:$textColor;
}
.textbox{
    width:100%;
    color:$textColor;
    position: absolute;
    top:50%;
    transform:translate(0,-50%);
    background-color:rgba(0,0,0,0.3);
    padding:20px;
    line-height:1.5rem;
}
